    padding: 0px 15px;
    font-weight: 500;
}

.imagens img {
    width: auto;
    height: 577px;
    margin-top: 61px;
    margin-left: 115px;
    position: absolute;
    overflow: hidden;
}

.foto {
    position: absolute;
    opacity: 0;
    animation-name: animacao;
    animation-duration: 20s;
    animation-iteration-count: infinite;
}

@keyframes animacao {
    25% {
        opacity: 1;
    }
    50% {
        opacity: 0;
    }
}

.foto:nth-child(1) {
    animation-delay: 5s;
}

.foto:nth-child(2) {
    animation-delay: 10s;
}

.foto:nth-child(3) {
    animation-delay: 15s;
}

.foto:nth-child(4) {
    animation-delay: 20s;
}


