.card2 {
    position: relative;
    border: 1px solid rgb(230, 222, 222);
    border-radius: 1px;
    margin: 0 0 10px;
    padding: 10px 0;
    margin-top: 1%;
    background-color: white;
    display: block;
    height: 45px;
    width: 340px;
    margin-left: auto;
    margin-right: auto;
    /* font-family: Verdana, Geneva, Tahoma, sans-serif; */
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

.texto {
    font-size: 14px;
    margin-left: 80px;
    margin-top: 2%;
    padding-top: 5px;
}

.texto span {
    font-size: 14px;
    color: #0095f6;
    font-weight: 400;
    margin-left: 1%;
}

.position {
    margin-top: 2%;
}

.app-container {
    margin-top: 1%;
    text-align: center;
    align-items: center;
    margin-left: auto;
    margin-right: auto;
    font-size: 14px;
}

.app-container p {
    margin-bottom: -30px;
}

.app-container img {
    width: auto;
    height: 140px;
}

@media screen and (max-width: 875px) {
    /* esconde em algum lugar */
    .card2 {
        justify-content: center;
        margin: auto;
    }
}

@media screen and (max-width: 875px) {
  
    /* esconde em algum lugar */
    .app-container {
        justify-content: center;
        margin: auto;
    }
    .card2{
        margin-top: 1.5%;
    }
}